.editor{display:flex;flex-direction:column;gap:20px;background-color:#fff;border-radius:20px;padding:30px}.editor__header{display:flex;align-items:center;justify-content:space-between}.editor__closer:hover{cursor:pointer}.editor__form{display:flex;flex-direction:column;gap:20px}.editor__avatar{display:flex;gap:20px}.editor__avatar-img{width:96px;height:96px;border-radius:100%}.editor__avatar-file{position:relative;padding:0 25px !important;min-width:224px}.editor__avatar-file::after{content:"Изображения, макс. 1 вложений.";position:absolute;left:0;bottom:-10px;transform:translateY(100%);display:block;color:#000;font-family:Roboto;font-size:12px;font-weight:400}.editor__login-label::before{content:"Новый логин";display:block;transform:translate(15px, 50%);font-family:Roboto;font-size:10px;font-weight:400;background-color:#fff;padding:0 10px;width:max-content}.editor__login-input{width:100%;height:56px;box-sizing:border-box;padding:9px 25px;font-family:Roboto;font-size:14px;font-weight:400;border-radius:4px;border:1px solid #ccc}.editor__login-input:focus{outline:none}.editor__saver{display:block;font-family:Roboto;font-size:14px;font-weight:400;color:#2667ff !important;margin-left:auto;border:none;background:none}.editor__saver:hover{text-decoration:underline;cursor:pointer}