.product-page__var {
    min-height: 170px;
    box-sizing: border-box;
    padding: 35px 35px 32px 25px;
    margin-bottom: 13px;

    &:last-child {
        margin-bottom: 0;
    }

    &-title {
        font-size: 20px !important;
        font-weight: 400 !important;
        margin-bottom: 37px !important;
    }

    &-descr {
        color: rgba(0, 0, 0, 0.60);
        font-family: Onest;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
    }

    &-cart {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 51px;
        height: 51px;
        border: 2px solid #2767FE;
        border-radius: 100%;
    } 
}

@media screen and (max-width: 1727px) {
    .product-page__var {
        &-title {
            font-size: 18px;
        }

        &-cart {
            width: 54px;
        }
    }
}